"use client";

import React, { useState, FormEvent } from 'react';
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogFooter,
} from '@/components/ui/dialog';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Alert, AlertDescription } from '@/components/ui/alert';

interface UpdateProductionModalProps {
  jobOrder: {
    id: string;
    produced: number;
    totalOrder: number;
  };
  onClose: () => void;
  onSave: (additionalProduced: number) => void;
}

const UpdateProductionModal: React.FC<UpdateProductionModalProps> = ({
  jobOrder,
  onClose,
  onSave,
}) => {
  const [additionalProduced, setAdditionalProduced] = useState<number>(0);
  const [error, setError] = useState<string>('');

  const handleInputChange = (value: string) => {
    // Allow empty string for clearing the input
    if (value === '') {
      setAdditionalProduced(0);
      return;
    }

    // Parse the value and round to 2 decimal places
    const numValue = parseFloat(value);
    if (!isNaN(numValue)) {
      // Round to 2 decimal places
      const roundedValue = Math.round(numValue * 100) / 100;
      setAdditionalProduced(roundedValue);
    }
  };

  const handleSubmit = (e: FormEvent) => {
    e.preventDefault();
    setError('');

    // Validate that additional quantity is greater than 0
    if (additionalProduced <= 0) {
      setError('Please enter a quantity greater than zero.');
      return;
    }

    // Round to 2 decimal places to ensure precision
    const roundedAdditional = Math.round(additionalProduced * 100) / 100;
    const newTotal = Math.round((jobOrder.produced + roundedAdditional) * 100) / 100;

    // Ensure total production does not exceed total order
    if (newTotal > jobOrder.totalOrder) {
      setError(`Total produced cannot exceed Total Order (${jobOrder.totalOrder}).`);
      return;
    }

    onSave(roundedAdditional);
  };

  return (
    <Dialog open onOpenChange={onClose}>
      <DialogContent className="sm:max-w-lg border-2 border-gray-300 shadow-2xl">
        <DialogHeader className="border-b border-gray-200 pb-4">
          <DialogTitle className="text-2xl font-bold text-black flex items-center">
            <div className="w-4 h-4 bg-red-500 rounded-full mr-3"></div>
            Update Production Quantity
          </DialogTitle>
          <p className="text-gray-600 mt-2">Job Order ID: <span className="font-semibold text-black">{jobOrder.id}</span></p>
        </DialogHeader>
        
        <form onSubmit={handleSubmit} className="space-y-6 pt-4">
          {error && (
            <Alert variant="destructive" className="border-red-500 bg-red-50">
              <AlertDescription className="text-red-700 font-medium">{error}</AlertDescription>
            </Alert>
          )}
          
          {/* Current Progress Display */}
          <div className="bg-gray-50 p-4 rounded-lg border border-gray-200">
            <h4 className="font-semibold text-black mb-3">Current Progress</h4>
            <div className="flex justify-between items-center mb-2">
              <span className="text-gray-700">Produced:</span>
              <span className="font-bold text-black">{jobOrder.produced.toFixed(2)}</span>
            </div>
            <div className="flex justify-between items-center mb-2">
              <span className="text-gray-700">Total Order:</span>
              <span className="font-bold text-black">{jobOrder.totalOrder.toFixed(2)}</span>
            </div>
            <div className="flex justify-between items-center mb-3">
              <span className="text-gray-700">Remaining:</span>
              <span className="font-bold text-red-600">{(jobOrder.totalOrder - jobOrder.produced).toFixed(2)}</span>
            </div>
            <div className="w-full bg-gray-200 rounded-full h-3">
              <div 
                className="h-3 rounded-full bg-black transition-all duration-300"
                style={{ width: `${(jobOrder.produced / jobOrder.totalOrder) * 100}%` }}
              ></div>
            </div>
            <span className="text-xs text-gray-600 mt-1 block text-center">
              {Math.round((jobOrder.produced / jobOrder.totalOrder) * 100)}% Complete
            </span>
          </div>
          
          <div className="space-y-3">
            <Label htmlFor="additionalProduced" className="text-black font-semibold text-sm uppercase tracking-wide">
              Additional Produced Quantity
            </Label>
            <Input
              id="additionalProduced"
              type="number"
              value={additionalProduced || ''}
              onChange={(e) => handleInputChange(e.target.value)}
              placeholder="Enter quantity to add (e.g., 10.50)"
              className="w-full border-2 border-gray-300 focus:border-red-500 focus:ring-red-500 transition-colors duration-200 h-12 text-lg"
              required
              min="0.01"
              max={jobOrder.totalOrder - jobOrder.produced}
              step="0.01"
            />
            <p className="text-sm text-gray-600 bg-gray-50 p-2 rounded border">
              <strong>Note:</strong> Maximum additional quantity: <span className="font-semibold text-red-600">{jobOrder.totalOrder - jobOrder.produced}</span>
            </p>
          </div>

          <DialogFooter className="gap-3 pt-6 border-t border-gray-200">
            <Button 
              type="submit" 
              className="bg-black hover:bg-gray-800 text-white px-6 py-3 font-semibold transition-all duration-200 transform hover:scale-105 border-2 border-red-500 hover:border-red-400"
            >
              Update Production
            </Button>
            <Button 
              type="button" 
              variant="outline" 
              onClick={onClose}
              className="border-2 border-gray-400 text-gray-700 hover:bg-gray-100 px-6 py-3 font-semibold transition-all duration-200"
            >
              Cancel
            </Button>
          </DialogFooter>
        </form>
      </DialogContent>
    </Dialog>
  );
};

export default UpdateProductionModal;
