import { clsx, type ClassValue } from "clsx"
import { twMerge } from "tailwind-merge"

export function cn(...inputs: ClassValue[]) {
  return twMerge(clsx(inputs))
}

export function generateJobOrderId(existingCount: number): string {
  // Pad the count to 3 digits. e.g., 1 becomes 001, 12 becomes 012
  return `JWO-${(existingCount + 1).toString().padStart(3, '0')}`;
}
