"use client";

import React, { useState } from 'react';
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from '@/components/ui/table';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import UpdateProductionModal from '@/components/UpdateProductionModal';
import { JobOrder } from '@/app/page';

interface JobWorkOrderTableProps {
  jobOrders: JobOrder[];
  onProductionUpdate: (id: string, additionalProduced: number) => void;
}

const JobWorkOrderTable: React.FC<JobWorkOrderTableProps> = ({ 
  jobOrders, 
  onProductionUpdate 
}) => {
  const [selectedOrder, setSelectedOrder] = useState<JobOrder | null>(null);

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'completed':
        return 'bg-black text-white border-2 border-white';
      case 'canceled':
        return 'bg-red-500 text-white border-2 border-red-600';
      default:
        return 'bg-white text-black border-2 border-red-500';
    }
  };

  const getProgressBarColor = (order: JobOrder) => {
    const percentage = (order.produced / order.totalOrder) * 100;
    if (percentage === 100) return 'bg-black';
    if (percentage >= 75) return 'bg-gray-700';
    if (percentage >= 50) return 'bg-gray-500';
    return 'bg-red-500';
  };

  return (
    <>
      <div className="rounded-lg border-2 border-gray-200 overflow-hidden shadow-lg">
        <Table>
          <TableHeader>
            <TableRow className="bg-black hover:bg-black">
              <TableHead className="text-white font-bold text-sm uppercase tracking-wide">ID</TableHead>
              <TableHead className="text-white font-bold text-sm uppercase tracking-wide">Fabric Name</TableHead>
              <TableHead className="text-white font-bold text-sm uppercase tracking-wide">Party Name</TableHead>
              <TableHead className="text-white font-bold text-sm uppercase tracking-wide">Yarn Used</TableHead>
              <TableHead className="text-right text-white font-bold text-sm uppercase tracking-wide">Total Order</TableHead>
              <TableHead className="text-right text-white font-bold text-sm uppercase tracking-wide">Produced</TableHead>
              <TableHead className="text-right text-white font-bold text-sm uppercase tracking-wide">Remaining</TableHead>
              <TableHead className="text-white font-bold text-sm uppercase tracking-wide">Progress</TableHead>
              <TableHead className="text-white font-bold text-sm uppercase tracking-wide">Status</TableHead>
              <TableHead className="text-white font-bold text-sm uppercase tracking-wide">Actions</TableHead>
            </TableRow>
          </TableHeader>
          <TableBody>
            {jobOrders.length === 0 ? (
              <TableRow>
                <TableCell colSpan={10} className="text-center text-gray-500 py-12">
                  <div className="flex flex-col items-center">
                    <div className="w-16 h-16 bg-gray-200 rounded-full flex items-center justify-center mb-4">
                      <div className="w-8 h-8 bg-red-500 rounded-full"></div>
                    </div>
                    <p className="text-lg font-medium">No job orders found</p>
                    <p className="text-sm">Create your first job order above to get started</p>
                  </div>
                </TableCell>
              </TableRow>
            ) : (
              jobOrders.map((order, index) => (
                <TableRow key={order.id} className={`${index % 2 === 0 ? 'bg-gray-50' : 'bg-white'} hover:bg-gray-100 transition-colors duration-150`}>
                  <TableCell className="font-bold text-black border-r border-gray-200">
                    <div className="flex items-center">
                      <div className="w-2 h-2 bg-red-500 rounded-full mr-2"></div>
                      {order.id}
                    </div>
                  </TableCell>
                  <TableCell className="font-medium text-gray-800 border-r border-gray-200">{order.fabricName}</TableCell>
                  <TableCell className="text-gray-800 border-r border-gray-200">{order.partyName}</TableCell>
                  <TableCell className="text-gray-800 border-r border-gray-200">{order.rawMaterial}</TableCell>
                  <TableCell className="text-right font-semibold text-black border-r border-gray-200">{order.totalOrder.toFixed(2)}</TableCell>
                  <TableCell className="text-right font-semibold text-black border-r border-gray-200">{order.produced.toFixed(2)}</TableCell>
                  <TableCell className="text-right font-semibold text-red-600 border-r border-gray-200">{order.remaining.toFixed(2)}</TableCell>
                  <TableCell className="border-r border-gray-200">
                    <div className="w-full bg-gray-200 rounded-full h-3">
                      <div 
                        className={`h-3 rounded-full transition-all duration-300 ${getProgressBarColor(order)}`}
                        style={{ width: `${(order.produced / order.totalOrder) * 100}%` }}
                      ></div>
                    </div>
                    <span className="text-xs text-gray-600 mt-1 block">
                      {Math.round((order.produced / order.totalOrder) * 100)}%
                    </span>
                  </TableCell>
                  <TableCell className="border-r border-gray-200">
                    <Badge 
                      variant="secondary" 
                      className={`${getStatusColor(order.status)} font-semibold uppercase text-xs px-3 py-1`}
                    >
                      {order.status}
                    </Badge>
                  </TableCell>
                  <TableCell>
                    <Button
                      variant="outline"
                      size="sm"
                      onClick={() => setSelectedOrder(order)}
                      disabled={order.status === 'completed' || order.status === 'canceled'}
                      className="border-2 border-black text-black hover:bg-black hover:text-white transition-all duration-200 font-semibold disabled:opacity-50 disabled:cursor-not-allowed"
                    >
                      Update
                    </Button>
                  </TableCell>
                </TableRow>
              ))
            )}
          </TableBody>
        </Table>
      </div>

      {selectedOrder && (
        <UpdateProductionModal
          jobOrder={selectedOrder}
          onClose={() => setSelectedOrder(null)}
          onSave={(additionalProduced) => {
            onProductionUpdate(selectedOrder.id, additionalProduced);
            setSelectedOrder(null);
          }}
        />
      )}
    </>
  );
};

export default JobWorkOrderTable;
