"use client";

import React, { useState } from 'react';
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import JobWorkOrderForm from '@/components/JobWorkOrderForm';
import JobWorkOrderTable from '@/components/JobWorkOrderTable';
import { generateJobOrderId } from '@/lib/utils';

export interface JobOrder {
  id: string;
  fabricName: string;
  partyName: string;
  rawMaterial: string;
  totalOrder: number;
  produced: number;
  remaining: number;
  status: 'pending' | 'completed' | 'canceled';
}

export default function Home() {
  const [jobOrders, setJobOrders] = useState<JobOrder[]>([]);

  const addJobOrder = (data: Omit<JobOrder, 'id' | 'produced' | 'remaining' | 'status'>) => {
    const newJobOrder: JobOrder = {
      id: generateJobOrderId(jobOrders.length),
      fabricName: data.fabricName,
      partyName: data.partyName,
      rawMaterial: data.rawMaterial,
      totalOrder: data.totalOrder,
      produced: 0,
      remaining: data.totalOrder,
      status: 'pending',
    };
    setJobOrders([...jobOrders, newJobOrder]);
  };

  const updateProduction = (id: string, additionalProduced: number) => {
    setJobOrders((prev) =>
      prev.map((order) => {
        if (order.id === id) {
          // Round to 2 decimal places to ensure precision
          const newProduced = Math.round((order.produced + additionalProduced) * 100) / 100;
          if (newProduced > order.totalOrder) {
            alert(`Produced quantity cannot exceed Total Order of ${order.totalOrder.toFixed(2)}`);
            return order;
          }
          const newRemaining = Math.round((order.totalOrder - newProduced) * 100) / 100;
          let newStatus: JobOrder['status'] = 'pending';
          if (newRemaining <= 0.001) { // Consider completed if remaining is very close to 0
            newStatus = 'completed';
          }
          return { ...order, produced: newProduced, remaining: newRemaining, status: newStatus };
        }
        return order;
      })
    );
  };

  return (
    <main className="container mx-auto p-6 min-h-screen bg-black">
      {/* Header Section */}
      <div className="text-center mb-8">
        <h1 className="text-5xl font-bold text-white mb-2">Job Work Management</h1>
        <div className="w-24 h-1 bg-red-500 mx-auto"></div>
        <p className="text-gray-300 mt-4 text-lg">Streamline your production workflow</p>
      </div>

      {/* Form Card */}
      <Card className="mb-8 bg-white border-2 border-gray-200 shadow-2xl">
        <CardHeader className="bg-gradient-to-r from-black to-gray-800 text-white">
          <CardTitle className="text-2xl font-bold flex items-center">
            <div className="w-3 h-3 bg-red-500 rounded-full mr-3"></div>
            Create New Job Order
          </CardTitle>
        </CardHeader>
        <CardContent className="p-6">
          <JobWorkOrderForm onSubmit={addJobOrder} />
        </CardContent>
      </Card>

      {/* Table Card */}
      <Card className="bg-white border-2 border-gray-200 shadow-2xl">
        <CardHeader className="bg-gradient-to-r from-black to-gray-800 text-white">
          <CardTitle className="text-2xl font-bold flex items-center justify-between">
            <div className="flex items-center">
              <div className="w-3 h-3 bg-red-500 rounded-full mr-3"></div>
              Active Job Orders
            </div>
            <div className="text-sm font-normal bg-red-500 px-3 py-1 rounded-full">
              {jobOrders.length} Total
            </div>
          </CardTitle>
        </CardHeader>
        <CardContent className="p-6">
          <JobWorkOrderTable jobOrders={jobOrders} onProductionUpdate={updateProduction} />
        </CardContent>
      </Card>
    </main>
  );
}
