"use client";

import React, { useState, FormEvent } from 'react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Alert, AlertDescription } from '@/components/ui/alert';

interface JobWorkOrderFormProps {
  onSubmit: (data: { 
    fabricName: string; 
    partyName: string; 
    rawMaterial: string; 
    totalOrder: number 
  }) => void;
}

const JobWorkOrderForm: React.FC<JobWorkOrderFormProps> = ({ onSubmit }) => {
  const [fabricName, setFabricName] = useState('');
  const [partyName, setPartyName] = useState('');
  const [rawMaterial, setRawMaterial] = useState('');
  const [totalOrder, setTotalOrder] = useState<number>(0);
  const [error, setError] = useState('');

  const handleSubmit = (e: FormEvent) => {
    e.preventDefault();
    setError('');

    if (!fabricName || !partyName || !rawMaterial || totalOrder <= 0) {
      setError('Please fill in all fields with valid values.');
      return;
    }

    onSubmit({ fabricName, partyName, rawMaterial, totalOrder });
    
    // Clear fields after successful submission
    setFabricName('');
    setPartyName('');
    setRawMaterial('');
    setTotalOrder(0);
  };

  return (
    <form onSubmit={handleSubmit} className="space-y-6">
      {error && (
        <Alert variant="destructive" className="border-red-500 bg-red-50">
          <AlertDescription className="text-red-700 font-medium">{error}</AlertDescription>
        </Alert>
      )}

      <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
        <div className="space-y-3">
          <Label htmlFor="fabricName" className="text-black font-semibold text-sm uppercase tracking-wide">
            Fabric Name
          </Label>
          <Input
            id="fabricName"
            type="text"
            value={fabricName}
            onChange={(e) => setFabricName(e.target.value)}
            placeholder="Enter fabric name"
            className="w-full border-2 border-gray-300 focus:border-red-500 focus:ring-red-500 transition-colors duration-200 h-12"
            required
          />
        </div>

        <div className="space-y-3">
          <Label htmlFor="partyName" className="text-black font-semibold text-sm uppercase tracking-wide">
            Party Name
          </Label>
          <Input
            id="partyName"
            type="text"
            value={partyName}
            onChange={(e) => setPartyName(e.target.value)}
            placeholder="Enter party name"
            className="w-full border-2 border-gray-300 focus:border-red-500 focus:ring-red-500 transition-colors duration-200 h-12"
            required
          />
        </div>

        <div className="space-y-3">
          <Label htmlFor="rawMaterial" className="text-black font-semibold text-sm uppercase tracking-wide">
            Yarn Used
          </Label>
          <Input
            id="rawMaterial"
            type="text"
            value={rawMaterial}
            onChange={(e) => setRawMaterial(e.target.value)}
            placeholder="Enter yarn used"
            className="w-full border-2 border-gray-300 focus:border-red-500 focus:ring-red-500 transition-colors duration-200 h-12"
            required
          />
        </div>

        <div className="space-y-3">
          <Label htmlFor="totalOrder" className="text-black font-semibold text-sm uppercase tracking-wide">
            Total Order Quantity
          </Label>
          <Input
            id="totalOrder"
            type="number"
            value={totalOrder || ''}
            onChange={(e) => setTotalOrder(Number(e.target.value))}
            placeholder="Enter total quantity (e.g., 100.50)"
            className="w-full border-2 border-gray-300 focus:border-red-500 focus:ring-red-500 transition-colors duration-200 h-12"
            required
            min="0.01"
            step="0.01"
          />
        </div>
      </div>

      <div className="flex justify-end mt-8">
        <Button 
          type="submit" 
          className="bg-black hover:bg-gray-800 text-white px-8 py-3 text-lg font-semibold transition-all duration-200 transform hover:scale-105 border-2 border-red-500 hover:border-red-400"
        >
          Create Job Order
        </Button>
      </div>
    </form>
  );
};

export default JobWorkOrderForm;
